/* Experience Section */
.experience {
    background-color: #f8f9fa;
}

.experience-content {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 4rem;
}

.qualifications h3,
.timeline h3 {
    color: #1a274c;
    margin-bottom: 2rem;
}

.qualification-item {
    display: flex;
    align-items: flex-start;
    gap: 1rem;
    margin-bottom: 2rem;
    padding: 1.5rem;
    background: white;
    border-radius: 12px;
    box-shadow: 0 5px 15px rgba(0, 0, 0, 0.1);
}

.qualification-item i {
    color: #1a274c;
    font-size: 1.5rem;
    margin-top: 0.25rem;
}

.qualification-item h4 {
    color: #1a274c;
    margin-bottom: 0.5rem;
}

.timeline-item {
    position: relative;
    padding-left: 2rem;
    margin-bottom: 2rem;
    border-left: 3px solid #1a274c;
}

.timeline-item::before {
    content: '';
    position: absolute;
    left: -8px;
    top: 0;
    width: 13px;
    height: 13px;
    border-radius: 50%;
    background-color: #1a274c;
}

.timeline-date {
    font-weight: 600;
    color: #1a274c;
    margin-bottom: 0.5rem;
}

.timeline-content h4 {
    color: #333;
    margin-bottom: 0.5rem;
}

/* Responsive Design */
@media (max-width: 768px) {
    .experience-content {
        grid-template-columns: 1fr;
        gap: 2rem;
    }
}